import { createTheme, CssBaseline, ThemeProvider } from '@mui/material'
import React, { Suspense } from 'react'
import BG from './assets/img/APLens-bg.png'
import { HashRouter, Routes, Route } from 'react-router-dom'
import { ROUTES } from './constants'
import utc from 'dayjs/plugin/utc'
import timezone from 'dayjs/plugin/timezone'
import dayjs from 'dayjs'

const HistoryDetail = React.lazy(() => import('./pages/history/HistoryDetail'))
const History = React.lazy(() => import('./pages/history'))
const WhitelistDetail = React.lazy(() => import('./pages/whitelist/WhitelistDetail'))
const Whitelist = React.lazy(() => import('./pages/whitelist'))
const User = React.lazy(() => import('./pages/user'))
const Login = React.lazy(() => import('./pages/login'))

dayjs.extend(utc)
dayjs.extend(timezone)

const theme = createTheme({
  palette: {
    primary: { main: '#0B4780' },
    secondary: { main: '#2E89FF' },
  },
  components: {
    MuiTableCell: {
      styleOverrides: {
        root: {
          color: '#0b4780',
          padding: '13px 0px',
          fontSize: '15px',
          lineHeight: '18px',
          borderColor: 'rgba(46, 137, 255, 0.5)',
        },
        head: {
          fontWeight: 'bold',
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'capitalize',
        },
      },
    },
  },
})

function App() {
  return (
    <ThemeProvider theme={theme}>
      <div
        style={{
          width: '100vw',
          height: '100vh',
          background: `url(${BG})`,
          backgroundSize: 'cover',
        }}
      >
        <CssBaseline />
        <Suspense fallback={<div>loading</div>}>
          <HashRouter>
            <Routes>
              <Route path={ROUTES.history}>
                <Route index element={<History />} />
                <Route path=':domainId' element={<HistoryDetail />} />
              </Route>
              <Route path={ROUTES.whitelist}>
                <Route index element={<Whitelist />} />
                <Route path=':whitelistId' element={<WhitelistDetail />} />
              </Route>
              <Route path={ROUTES.user} element={<User />} />
              <Route path='/' element={<Login />} />
            </Routes>
          </HashRouter>
        </Suspense>
      </div>
    </ThemeProvider>
  )
}

export default App
